body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
}
body {
  max-width: 1000px;
  margin: 0 auto;
}
.nav {
  background-color: #31e6e9;
  height: 40px;
  padding: 10px;
  max-width:720px ;
}
#navhead {
  margin-left: 10%;
}
img {
  height: 100px;
  width: 100px;
  border-radius: 100%;
}
header {
  background-color: rgb(38, 201, 215);
  width: 20%;
  min-width: 200px;
  display: inline-block;
  vertical-align: top;
}
#headmain {
  background-color: rgb(31, 119, 235);
  width: 11em;
  padding: 6%;
}
ul > li,
h5 {
  margin-bottom: 10px;
  margin-left: 5px;
  list-style: none;
}
.contact {
  margin-top: 5px;
  max-width: 150px;
}
.education {
  margin-top: 10px;
  padding: 10px;
}
.item {
  margin-top: 10px;
}

/* Media Query for Small Screens */
@media (max-width: 768px) {
.main{
  display: block;
  /* width: 100%; */
}    
header{
  display: block;
  width: 540px;
}
#headmain {
  width: 475px;
}
.maindiv {
  width: 100%;
}
.nav{
  width: 520px;
}
}

/* Flexbox Layout */
#maindiv {
  /* display: flex;
  flex-direction: row;  */
}

.main {
  display: inline-block;
  /* flex: 1; Take remaining space */
  background: #35eeff;
  color: rgb(21, 11, 11);
  padding: 20px;
  /* color: white; */
  width: 500px;
  /* margin-left: 0px; */
}